.contacts
{
    margin-left: 15%;
    width: 100%;
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    margin: 5% auto;
    padding-left: 20%;
    color: white;
    font-family: "Alexandria", sans-serif;
    font-family: 'Poppins';
}
.contactsTitle
{
    width: 100%;
    font-family: "Alexandria", sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    :first-child{

        font-size: 40px;
    }
    :last-child
    {
        font-weight: 600;
        border-radius: 1px;
        color: rgba(0, 0, 0, 0.1); 
        -webkit-text-stroke: 0.4px rgb(80, 80, 80);
        font-size:calc(60px + 12 * (100vw / 1980));
    }
}
.contactsBody
{
    display: flex;
    padding: 20px;
}

.contactsBodyFieds
{
    width: 70%;
    & input {
        border:none;
       border-bottom:1px solid rgb(255, 255, 255);
       cursor: pointer;
       outline: none;
       color: white;
       width: 80%;
       margin: 5% 0px;
       font-size: 20px;
       font-weight: 100;
       background-color: transparent;
    }

}

.contactsBodyTextContact
{
    width: 40%;
}
.contactsBodyTextContact__title
{
    & p
    {
        font-size: 30px;
        padding-bottom: 4%;
    }
}
.contactsBodyTextContact__street
{

    & p
    {
        font-size: 17px;
        padding-bottom: 4%;
        color: #797979;
    }
}
.contactsBodyTextContact__phone
{
    display: flex;
    flex-direction: column;

    & a
    {
        text-decoration: none;
        font-size: 17px;
        padding-bottom: 3%;
        color: rgb(255, 255, 255);
        font-family: "Poppins";
        font-weight: 400;
    }
}
.contactsBodyTextContact__icons
{
    display: flex;
    margin-top: 4%;
    :first-child
    {
        margin-left: 0;
    }
    & li{
        margin: 0 10px;
        width: 40px;
        height: 40px;
        background-color: #333;
        border-radius: 50%;
        transition: transform 500ms ease;      
    }
}

.contactsBodyTextContact__icons li svg
{
    color: white;
    position: relative;
    top: 11px;
    right: -12px;   
}
.contactsBodyTextContact__icons li:hover
{
    transform: scale(1.3);
    background-color: yellow;
    & svg
    {
        color: black;
    }
}
.contactsButton
{
    width: 60%;
    font-size: 20px;
    padding: 15px;
    background-color: transparent;
    border: 1px;
    border: 1px solid #FDDD0A;
    color: #FDDD0A;
    text-transform: uppercase;
    margin-top: 5%;
    text-transform: uppercase;
    font-size:calc(10px + 12 * (100vw / 1980));
    max-width: none;
    background: linear-gradient(to left, transparent 50%, #FDDD0A 50%) right;
    background-size: 200%;
    transition: .3s ease-out;
}
.contactsButton:hover
{
    background-position: left;
    color: black;
}

.contactsBodyFieds__file
{
    width: 80%;
}
.inputFile {
	position: relative;
	display: flex;
    justify-content: space-between;
    margin-top: 0px;
    margin: 0;
    border-bottom:1px solid rgb(255, 255, 255);
    padding-bottom: 4%;
    align-items: center;
    transition: all 500ms ease;
    border-bottom: 0;

}
.inputFileButton {
	position: relative;
	display: inline-block;
	cursor: pointer;
	font-size: 12px;
	color: black;
    text-transform: uppercase;
    font-weight: 500;
    background-color: #f2f2f2;
	text-align: center;
	border-radius: 20px;
	line-height: 20px;
	height: 30px;
	padding: 5px 15px;
	box-sizing: border-box;
	border: none;
	margin: 0;
    transition: all 500ms ease;
    
}
.input-file-text {
	padding: 0 10px;
	line-height: 40px;
	display: inline-block;
}

.inputFile input[type=file] {
	position: absolute;
	z-index: -1;
	opacity: 0;
	display: block;
	width: 0;
	height: 0;

}
 
.inputClip
{
    transition: all 500ms ease;
    width: 20px;
    height: 30px;
    margin-right: 2%;
}
 
.inputClip:hover
{
    transform: scale(1.5);
}
 
/* Hover/active */
.inputFile:hover .inputFileButton {
	border: 1px solid silver;
    transform: scale(1.1);

}
.inputFile:active .inputFileButton {
	border-color: silver;
    transform: scale(1.1);

}
/* Disabled */
.inputFile input[type=file]:disabled + .inputFileButton {
	background-color: #eee;
}
@media screen and (max-width: 1240px)
{
	
}
@media screen and (max-width: 670px)
{
	.contactsTitle >p
    {
        font-size: calc(50px + 1*(100vw / 1980)) !important;
    }
    .contacts
    {
        padding-left: 5%;
    }
    .contactsBodyFieds
    {
    width: 100%;
    margin-bottom: 10%;
    & input {
        border:none;
       border-bottom:1px solid rgb(255, 255, 255);
       cursor: pointer;
       outline: none;
       color: white;
       width: 90%;
       margin: 5% 0px;
       font-size: 20px;
       font-weight: 100;
       background-color: transparent;
    }

    }
    .contactsBodyFieds__file
    {
        width: 90%;
    }
    .contactsBodyTextContact
    {
        width: 100%;
    }
}
@media screen and (max-width: 900px)
{
	.contactsBody
    {
        flex-direction: column;
        padding: 0;
    }
}
